'use strict';

import app from "../app";
import "../libs/tools";

/**
 * userModel usermodel
 */
app.factory("userModel",["$resource","todoTools",function ($resource,todoTools) {
    return {
        /**
         * 
         * @param data
         * @returns {*|Function}
         */
        login:function(data) {
            return $resource(todoTools.apiUrl('login')).save(data).$promise;
        }
    }
}]);


